* {
  font-family: sans-serif;
}
body {
  background: #ffeeee;
  padding: 20px 40px;
  margin: 0;
}

h2 {
  padding: 20px 0;
  color: darkslateblue;
}

h2:first-child {
  padding-top: 0;
}

section {
  margin-bottom: 4em;
}

.two-cols {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  align-items: start;
}

.two-cols > div {
  border: 10px solid rgba(0, 0, 0, 0.2);
  padding: 1em 1.5em;
  border-radius: 5px;
}

textarea {
  width: 100%;
  background: #13222b;
  border: none;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 5px;
  color: aliceblue;
  font-family: monospace;
  min-height: 750px;
  font-size: 16px;
}

img {
  max-width: 100%;
}

hr {
  margin-top: 50px;
  border: 0 solid rgba(0, 0, 0, 0.3);
  border-top-width: 1px;
}
